import { Pipe, Injectable, PipeTransform } from '@angular/core';
import { Product } from './../../../core/models/product';

/**
  * Filter the products based on selected taxons in the sidebar
  * @name filter
  * @param selectedTaxonids
  */
@Pipe({
  name: 'filter'
})
@Injectable()
export class FilterPipe implements PipeTransform {
  transform(products: Product[], selectedTaxonIds: number[], priceSet: number): any[] {
    const selectedIds = selectedTaxonIds;
    console.log("pipe", priceSet);
    if (!products) return [];
    if (!selectedIds || selectedIds.length === 0) return products;
    return products.filter(product => {
      console.log("product", product);
      let productPresent = false;
      selectedIds.forEach(id => {
        if (product.taxon_ids.findIndex(taxon_id => taxon_id === id ) !== -1) {
          productPresent = true;
        }
      });

      let productPresent2 = false;
      product.product_price_sets.forEach(set=>{
        if (priceSet == set.id) productPresent2 = true;
      });

      if (priceSet == undefined) productPresent2 = true;

      return productPresent && productPresent2;
    });
  }
}


